import {BrowserRouter, Routes, Route} from "react-router-dom";
import Coin from "./Coin";
import Coins from "./Coins";

/**
 * react 6  declare      .
 */
function Router(){
    return (
    <BrowserRouter basename={process.env.PUBLIC_URL}>
        <Routes>
            <Route
                path={`/:coinId/*`}
                element={<Coin/>}
            />
        </Routes>
        <Routes>
            <Route
                path={`/`}
                element={<Coins/>}
            />
        </Routes>
    </BrowserRouter>
    );
}
export default Router;